import React from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import "bootstrap/dist/js/bootstrap.bundle.min.js";
import Login from "./Components/Login";
import ForgotPassword from "./Components/ForgotPassword";
import FirstLogin from "./Components/FirstLogin";
import Salesman from "./Components/Salesman";
import ResetPassword from "./Components/ResetPassword";
import RestrictedRoute from "./Auth/RestrictedRoute";
import PrivateRoute from "./Auth/PrivateRoute";
import { BrowserRouter, Route, Switch } from "react-router-dom";
import Admin from "./Components/Admin";
import Round from "./Components/Round";
import MaintainProfile from "./Components/Maintainprofile";
import UploadData from "./Components/UploadData";
import RoundSetting from "./Components/RoundSetting";
import WholesellerPage from "./Components/WholesellerPage";
import SalesmanLeaderboard from "./Components/SalesmanLeaderboard";
import HeaderNew from "./Components/HeaderNew";
import PhoneNumber from "./Components/PhoneNumber";

function App() {
  return (
    <BrowserRouter>
      <Switch>
        <RestrictedRoute exact path="/" component={Login} />
        <Route exact path="/forgotpassword" component={ForgotPassword} />
        <Route exact path="/resetpassword" component={ResetPassword} />
        <Route exact path="/firstlogin" component={FirstLogin} />
        <Route exact path="/salesman" component={Salesman} />
        <Route exact path="/admin" component={Admin} />
        <Route exact path="/round" component={Round} />
        <Route exact path="/maintainprofile" component={MaintainProfile} />
        <Route exact path="/uploaddata" component={UploadData} />
        <Route exact path="/wholeseller" component={WholesellerPage} />
        <Route exact path="/leaderboards" component={SalesmanLeaderboard} />
        <Route exact path="/roundsetting" component={RoundSetting} />
        <Route exact path="/updatephonenumber" component={PhoneNumber} />



      

      </Switch>
    </BrowserRouter>
  );
}

export default App;
