import {FC , useState, useEffect } from "react"
import { RouteComponentProps, useLocation } from "react-router"
import axios from "axios"
import { Link } from "react-router-dom"
import HeaderAdmin from "./HeaderAdmin"
import paths from "../ApiServices.json";


import "./leaderboard.css"
import Header from "./Header"
import PopupReset from "./PopupReset"
import { TextField } from "@mui/material"
import { Console } from "console"
type SomeComponentProps = RouteComponentProps
const MaintainProfile: FC<SomeComponentProps> = ({ history }) => {
    const [urlResetAdmin, setUrlResetAdmin] = useState(""); 
    // search feature
    let [employeList, setEmployeeList] = useState<any[]>([])
    let [filterEmployee, setfilterEmployee] = useState<any[]>([])
    var urlData = "";
    let inputHandler = (e : any) => {
        console.log("anas")
  
        var lowerCase = e.target.value.toLowerCase()
        if(lowerCase == ''){
            setfilterEmployee(employeList);
        } else{
            
            let filterData = employeList.filter(data => {
                return data.employeeCode.toLowerCase().includes(lowerCase) || data.employeeName.toLowerCase().includes(lowerCase) ;
            })
            setfilterEmployee(filterData);
            console.log("filteredData", filterData)
        }
        console.log("employeeList",employeList)
    }


    const [modalShow, setModalShow] = useState(false); 

    const getUserList = paths.admin.getUserList;
    const getResetAdmin =  paths.admin.resetAdmin;
    
    

    const getProfile = () => {
        console.log("getProfile")
        const USER_TOKEN = localStorage.getItem("auth")
        const AuthStr = 'Bearer ' + USER_TOKEN
        console.log(AuthStr)
        axios
        .get(getUserList, {headers: {Authorization: AuthStr,"Access-Control-Allow-Origin":"*"}})
        .then((res) => {
            console.log(res.data.data)
            setEmployeeList(res.data.data)
            setfilterEmployee(res.data.data)
        })
    }

    const getUrl = (customerCode : any) => {
        const USER_TOKEN = localStorage.getItem("auth")
        const AuthStr = 'Bearer ' + USER_TOKEN
        console.log(AuthStr)
        axios
        .put(getResetAdmin + customerCode,{}, {headers: {Authorization: AuthStr,"Access-Control-Allow-Origin":"*"}})
        .then((res) => {
            console.log("resetAdminResponse",res.data.data.url)
            setUrlResetAdmin(res.data.data.url)
            urlData = res.data.data.url;
        })
    }

    useEffect(() => {
        getProfile()
        console.log(employeList)
    }, [])
    
    return (
        <>
         <Header />
        <div className="main-form">
            <div className="container">                    
                <div className="reward">
                    <div className="row">
                                    
                                    <div className="col ms-1" style={{
                                        paddingLeft: "0px",
                                        fontSize: "larger"
                                    }}>
                                        <Link to="/admin">
                                        <i className="fa fa-arrow-left m-3" />
                                        </Link>
                                        <label className="p-3" style={{marginLeft: "-20px"}}><b>Atur Ulang Sandi</b></label>
                                    </div>
                                </div>
                                    
                    <div className="search pt-1">
                        <div className="input-group">
                            {/* <span className="input-group-text" id="basic-addon1"><i className="fa fa-search"></i></span> */}
                            <TextField  id="outlined-basic" onChange={inputHandler} variant="outlined" fullWidth label="Search"/>
                            {/* <input type="text" onChange={inputHandler} className="form-control" placeholder="Find name or area" aria-label="Username" aria-describedby="basic-addon1" /> */}
                        </div>
                    </div>
                            
                        <div className="row">
                            <div className="list">
                                {filterEmployee && filterEmployee.length > 0 && filterEmployee.map((employee) => (
                                    <div key={employee.employeeCode}>
                                        <div className="list-item-maintain">
                                            <div className="col-2">
                                                <p style={{
                                                    marginTop: "0px"
                                                }}><label>{employee.employeeCode}</label></p>
                                            </div>
                                            <div className="col-4 text-center">
                                                <label>{employee.employeeName}</label>
                                            </div>                        
                                            <div className="col d-block text-end">
                                                <a onClick={()=>{getUrl(employee.employeeCode);setModalShow(!modalShow);}} className="btn btn-oval">Reset</a>
                                                {/* <button onClick={()=>setModalShow(!modalShow)} className="btn btn-oval">Reset</button> */}
                                                {modalShow? <PopupReset url= {urlResetAdmin} show={modalShow} onHide={() => setModalShow(false)} /> : null}
                                            </div>                                            
                                            </div>
                                            </div>
                                        ))}
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
        </>
    )
}
export default MaintainProfile