import React from 'react';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';

const PopupSalesman= (props : any) => {
    const data = {
        employeeName: localStorage.getItem("nameSalesman"),
        wholesellerDetail: JSON.parse(localStorage.getItem("wholesellerDetailSalesman") || '{"pointA":"senpai","pointB":"senpai","pointC":"senpai"}'),
    };

    //   const dataSalesman = {
    //     salesmanName : localStorage.getItem("salesmanName"),
    //     salesmanDetail : JSON.parse(localStorage.getItem("salesmanDetail") || '{"pointA":"senpai","pointB":"senpai","pointC":"senpai"}')

    //   }

  return (
    <Modal
      {...props}
      size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
            Informasi Detail
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        {/* <h4>Centered Modal</h4>
        <p>
          Cras mattis consectetur purus sit amet fermentum. Cras justo odio,
          dapibus ac facilisis in, egestas eget quam. Morbi leo risus, porta ac
          consectetur ac, vestibulum at eros.
        </p> */}
<div>


<tr>
  <td>Wholesaler Name : {data.employeeName}</td>
</tr>

{/* <tr>
  <td>Salesman Name : {dataSalesman.salesmanDetail}</td>
</tr> */}


<tr>
  <td>Point Tetap : {(data.wholesellerDetail.pointA).toLocaleString("id_Id")}</td>
</tr>
<tr>
  <td>Potensi Point : {(data.wholesellerDetail.pointB).toLocaleString("id_Id")}</td>
</tr>
{/* <tr>
  <td>Point C : {data.wholesellerDetail.pointC}</td>
</tr> */}

<tr>
  <td>Total Point : {(data.wholesellerDetail.salePoint).toLocaleString("id_Id")}</td>
</tr>
<tr>
  <td>Baseline Stock : {(data.wholesellerDetail.baselineStock).toLocaleString("id_Id")}</td>
</tr>

<tr>
  <td>Last Update : {data.wholesellerDetail.latestUpdate.replace("T00:00:00", "")}</td>
</tr>
</div>
      </Modal.Body>
      <Modal.Footer>
        <Button onClick={props.onHide}>Close</Button>
      </Modal.Footer>
    </Modal>
  );
}

